export default {
	data() {
		return {
			selected:[]
		};
	},
	computed:{
		
	},
	methods:{
		selectedChoice(choice){
			if(this.selected.indexOf(choice) == -1){
				this.selected.push(choice)
			}else{
				this.selected.splice(this.selected.indexOf(choice), 1); //取消
			}
			
			
			
		}
	},
	props:{
		choices:{
			type:Array
		},
		title:{
			type:String
		}
	}
	
}
